/*======================TODO TABLE STYLES =============================== */
/* Table column sizing
  ================================== */
#todo-list-flex-table .tags-cell {
  width: 10%;
}

#todo-list-flex-table .text-cell {
  width: 50%;
}

#todo-list-flex-table .date-cell {
  width: 25%;
}

#todo-list-flex-table .icon-cell {
  width: 15%;
}

/* Apply styles Main Table
    ================================== */
#todo-list-flex-table .flexTable {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 3em 0;
  padding: 0;
  /* box-shadow: 0 0 40px rgba(0, 0, 0, 0.2); */
}

#todo-list-flex-table.flexTable .flexTable-row {
  width: 100%;
  display: flex;

  overflow: hidden;
  margin-bottom: 0.5rem;
  border-radius: 0.4rem;
  background-color: #f7f7f7;
  transition: background 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);

  font-family: 'Lato', sans-serif;
  font-weight: 300;
}

#todo-list-flex-table.flexTable .flexTable-row.flexTable-row--head {
  background-color: white;
  border: none;
  transition: none;
}
#todo-list-flex-table.flexTable .flexTable-row.flexTable-row--head:hover {
  background-color: white;
  border: none;
  box-shadow: none;
}

#todo-list-flex-table.flexTable .flexTable-row:hover {
  background-color: #ebebeb;
  font-weight: 400;

  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}


#todo-list-flex-table.flexTable .flexTable-row-completed {
  border-left: 5px solid #28a745;
}

#todo-list-flex-table.flexTable .flexTable-row .flexTable-cell {
  box-sizing: border-box;
  flex-grow: 1;
  padding: 0.8em 1.2em;
  overflow: hidden;
  list-style: none;
}
#todo-list-flex-table.flexTable .flexTable-row .flexTable-cell.column-heading {
  /* background-color: #43bac0; */
  background-color: #fff;
  color: #000;
  font-size: 18px;
  line-height: 1.4;

  font-family: 'Lato', sans-serif;
  font-weight: 700;
  padding: 1em;
}

#todo-list-flex-table.flexTable .flexTable-row .flexTable-cell .flexTable-cell--heading {
  display: none;
}

#todo-list-flex-table .icon-content > svg {
  margin-left: 10px;

  color: #333a40;
  transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

#todo-list-flex-table .icon-content > svg:hover {
  color: #000;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

#todo-list-flex-table.flexTable .flexTable-row-active {
  border: 2px solid #000;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  background-color: #ebebeb;
  animation: active-row 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

#todo-list-flex-table.flexTable .flexTable-row-active.flexTable-row-deleted {
  border-left: 5px solid red;
}

#todo-list-flex-table.flexTable .flexTable-row--head {
  margin-bottom: 0;
}

@keyframes active-row {
  0% {
    border-color: #fff;
  }

  100% {
    border-color: #333a40;

    /* transform: translateY(0); */
  }
}

/* Responsive Todo Table
  ==================================== */
@media all and (max-width: 750px) {
  #todo-list-flex-table.flexTable {
    display: block;
    width: 100%;
    box-shadow: none;
  }
  #todo-list-flex-table .flexTable-row {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.2);
  }

  #todo-list-flex-table.flexTable .flexTable-row .flexTable-cell {
    width: 100% !important;
    display: flex;
    align-items: center;
    padding: 0;
    border-bottom: 1.1px solid #706a6a38;
  }
  #todo-list-flex-table.flexTable
    .flexTable-row
    .flexTable-cell
    .flexTable-cell--heading {
    display: inline-block;
    flex: .6 1;

    background-color: #fff;
    color: #000;
    font-size: 14px;
    line-height: 1.4;

    font-family: 'Lato', sans-serif;
    font-weight: 700;
  }

  #todo-list-flex-table.flexTable .flexTable-cell.icon-cell {
    border-bottom: 1.1px solid #706a6a38;

    order: -1;
  }
  #todo-list-flex-table.flexTable .flexTable-cell--heading {
    padding: 10px 20px;
    margin: 0;
  }

  #todo-list-flex-table.flexTable
    .flexTable-row
    .flexTable-cell
    .flexTable-cell--content {
    flex: 2 1;
    padding-left: 0.5rem;
    font-size: 12px;
  }
  .tags-cell {
    padding-left: 0.5rem;
  }
  #todo-list-flex-table.flexTable .icon-content {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  #todo-list-flex-table.flexTable .icon-content > svg {
    margin-right: 1rem;
  }

  #todo-list-flex-table.flexTable .flexTable-row--head {
    display: none;
  }
}
.actionBtn{
  width: 6rem;
  height: 2rem;
  background: deepskyblue;
  color: #fff;
  margin: 0rem 0.2rem;
}


.todo-list-table-pagination {
  margin-top: 2rem;
}
.todo-list-table-pagination .page-link {
  background-color: #ffffff;
  color: #12aaeb;
  padding: 0.7rem 1.25rem;
  transition: all 0.25s ease-in;
  font-weight: 700;
}

.todo-list-table-pagination .page-link:hover {
  z-index: 2;
  color: #000;
  font-family: 700;
  text-decoration: none;
  background-color: #e9ecef;
  border-color: #dee2e6;
}
.todo-list-table-pagination .page-item.active .page-link {
  z-index: 3;
  color: #fff;
  background-color: #174260;
  border-color: #174260;
}

.todo-list-table-pagination.pagination {
  background: transparent;
}

/* Responsive Pagination
  ==================================== */
@media all and (max-width: 750px) {
  .todo-list-table-pagination {
    display: flex;
    flex-flow: wrap;
  }

  .todo-list-table-pagination .page-item:first-child {
    display: none;
  }

  .todo-list-table-pagination .page-item {
    padding: 2px;
  }

  .todo-list-table-pagination .page-link {
    padding: 5px 10px;
    text-align: center;
  }
  .todo-list-table-pagination .page-item:last-child {
    display: none;
  }
}

.todo-button {
  font-family: Open Sans, 'Helvetica', 'Arial', sans-serif;
  padding-top: 0.30952381em;
  padding-bottom: 0.30952381em;
  padding-right: 1.85714286em;
  padding-left: 1.85714286em;
  background: #12aaeb;
  border-radius: 0;
  border: 1px solid #12aaeb;
  border-width: 1px;
  font-size: inherit;
  line-height: 1.85714286em;
  text-transform: uppercase;
  color: #fff;
  font-weight: 700;
  letter-spacing: 0.5px;
}
.add-todo-button {
  position: absolute;
  top: 6rem;
  right: 10rem;
}

.todo-button:hover,
.todo-button:focus {
  transform: translate3d(0, -2px, 0);
  -webkit-transform: translate3d(0, -2px, 0);
  background: #28b3ef !important;
  border-radius: 0;
  border: 1px solid #12aaeb !important;
}

.todo-button:visited {
  background: #12aaeb !important;
  border-color: #12aaeb !important;
}
#todo-list-flex-table.flexTable .flexTable-row .flexTable-cell {
  width: 100%;
}
#todo-list-flex-table .icon-content > svg {
  color: #12aaeb;
}
#user-list-flex-table .icon-content > svg:hover {
  color: #28b3ef;
}


@media all and (max-width: 992px) {
  .add-todo-button {
    position: absolute;
    top: 20px;
    right: 20%;
    font-size: 1em;
  }
}

@media all and (max-width: 750px) {
  .add-todo-button {
    position: static;
  }
}

